    aeGetCurrentHeight: async function () {
      try {
        const height = await aeSdk.height();
        this.responseSucess("aeGetCurrentHeight", { height: height });
      } catch (e) {
        this.responseError("aeGetCurrentHeight", e.message);
      }
    },

    aeGenerateAccount: async function () {
      let mnemonic = bip39.generateMnemonic();

      const seed = await bip39.mnemonicToSeed(mnemonic);
      const wallet = getHdWalletAccountFromSeed(seed, 0);
      this.responseSucess("aeGenerateAccount", {
        mnemonic: mnemonic,
        publicKey: wallet.publicKey,
        secretKey: wallet.secretKey,
      });
    },

    aeRestoreAccountMnemonic: async function (mnemonic) {
      if (!bip39.validateMnemonic(mnemonic)) {
        this.responseError(
          "aeRestoreAccountMnemonic",
          "mnemonic validate error"
        );
        return;
      }

      const seed = await bip39.mnemonicToSeed(mnemonic);
      const wallet = getHdWalletAccountFromSeed(seed, 0);
      this.responseSucess("aeRestoreAccountMnemonic", {
        mnemonic: mnemonic,
        publicKey: wallet.publicKey,
        secretKey: wallet.secretKey,
      });
    },

    aeRestoreAccountSecretKey: async function (secretKey) {
      try {
        const publicKey = getAddressFromPriv(secretKey);
        this.responseSucess("aeRestoreAccountSecretKey", {
          publicKey: publicKey,
          secretKey: secretKey,
        });
      } catch (e) {
        this.responseError("aeRestoreAccountSecretKey", e.message);
      }
    },

    aeBalance: async function (address) {
      try {
        let balance = await aeSdk.getBalance(address);
        balance = ethers.utils.formatUnits(balance, 18);
        this.responseSucess("aeBalance", { balance: balance });
      } catch (e) {
        this.responseError("aeBalance", e.message);
      }
    },

    aeSpend: async function (secretKey, receiveAddress, amount, payload) {
      try {
        const publicKey = getAddressFromPriv(secretKey);
        const senderAccount = new MemoryAccount({
          keypair: { secretKey: secretKey, publicKey: publicKey },
        });
        await aeSdk.addAccount(senderAccount, { select: true });
        let spendTxResult = await aeSdk.spend(amount, receiveAddress, {
          denomination: AE_AMOUNT_FORMATS.AE,
          payload: Base64.decode(payload),
        });
        this.responseSucess("aeSpend", { hash: spendTxResult.hash });
      } catch (e) {
        this.responseError("aeSpend", e.message);
      }
    },

    aeAensUpdate: async function (secretKey, name) {
      try {
        let nameInfo = await aeSdk.api.getNameEntryByName(name);
        if (nameInfo == null) {
          this.responseError("aeAensUpdate", "name is not exist");
          return;
        }
        const publicKey = getAddressFromPriv(secretKey);
        const senderAccount = new MemoryAccount({
          keypair: { secretKey: secretKey, publicKey: publicKey },
        });

        const pointers = {
          account_pubkey: publicKey,
        };
        await aeSdk.addAccount(senderAccount, { select: true });
        let spendTxResult = await aeSdk.aensUpdate(name, pointers, {});
        this.responseSucess("aeAensUpdate", { hash: spendTxResult.hash });
      } catch (e) {
        this.responseError("aeAensUpdate", e.message);
      }
    },

    aeAensClaim: async function (secretKey, name) {
      let publicKey;
      try {
        if (!(await this.getNameInfo(name))) {
          this.responseError("aeAensClaim", "Name is registered");
          return;
        }
        publicKey = getAddressFromPriv(secretKey);
        const senderAccount = new MemoryAccount({
          keypair: { secretKey: secretKey, publicKey: publicKey },
        });
        await aeSdk.addAccount(senderAccount, { select: true });
        const preClaimTx = await aeSdk.aensPreclaim(name);
        const claimTx = await aeSdk.aensClaim(name, preClaimTx.salt, {
          verify: true,
          waitMined: false,
        });

        this.responseSucess("aeAensClaim", { hash: claimTx.hash });
      } catch (e) {
        this.responseError("aeAensClaim", e.message);
      }
    },

    getNameInfo: async function (name) {
      try {
        await aeSdk.api.getNameEntryByName(name);
        return false;
      } catch (e) {
        return true;
      }
    },

    aeAensTransfer: async function (secretKey, name, recipient) {
      try {
        let nameInfo = await aeSdk.api.getNameEntryByName(name);
        if (nameInfo == null) {
          this.responseError("aeAensTransfer", "name is not exist");
          return;
        }
        const publicKey = getAddressFromPriv(secretKey);
        const senderAccount = new MemoryAccount({
          keypair: { secretKey: secretKey, publicKey: publicKey },
        });
        await aeSdk.addAccount(senderAccount, { select: true });
        let spendTxResult = await aeSdk.aensTransfer(name, recipient);
        this.responseSucess("aeAensTransfer", { hash: spendTxResult.hash });
      } catch (e) {
        this.responseError("aeAensTransfer", e.message);
      }
    },

    aeAex9TokenBalance: async function (address, ctAddress) {
      try {
        let aex9Contract = await aeSdk.getContractInstance({
          aci: Aex9TokenContract,
          contractAddress: ctAddress,
        });

        let metaInfoTx = await aex9Contract.methods.meta_info();
        let metaInfoResult = await metaInfoTx.decodedResult;

        let balanceTx = await aex9Contract.methods.balance(address);
        let balanceResult = await balanceTx.decodedResult;

        let balance = ethers.utils.formatUnits(
          balanceResult,
          metaInfoResult.decimals
        );

        this.responseSucess("aeAex9TokenBalance", {
          address: address,
          ctAddress: ctAddress,
          decimals: metaInfoResult.decimals,
          balance: balance,
        });
      } catch (e) {
        this.responseError("aeAex9TokenBalance", e.message);
      }
    },
