        await aeSdk.addAccount(senderAccount, { select: true });
        let aex9Contract = await aeSdk.getContractInstance({
          aci: Aex9TokenContract,
          contractAddress: ctAddress,
        });

        let balanceTx = await aex9Contract.methods.transfer(
          toAddress,
          toAettos(amount),
          {
            waitMined: false,
          }
        );

        this.responseSucess("aeAex9TokenTransfer", {
          publicKey: publicKey,
          ctAddress: ctAddress,
          hash: balanceTx.hash,
        });
      } catch (e) {
        this.responseError("aeAex9TokenBalance", e.message);
      }
    },

