    aeVegasProvideAnswer: async function (
      secretKey,
      ctAddress,
      owner,
      marketId,
      selectIndex,
      marketType
    ) {
      try {
        const publicKey = getAddressFromPriv(secretKey);
        const senderAccount = new MemoryAccount({
          keypair: { secretKey: secretKey, publicKey: publicKey },
        });
        await aeSdk.addAccount(senderAccount, { select: true });
        let vegasContract = await aeSdk.getContractInstance({
          aci: VegasMarketContract,
          contractAddress: ctAddress,
        });

        let provideAnswerTx;
        if (marketType == 0) {
          provideAnswerTx =
            await vegasContract.methods.private_update_market_progress_to_over(
              owner,
              marketId,
              selectIndex
            );
        } else {
          provideAnswerTx = await vegasContract.methods.provide_answer(
            owner,
            marketId,
            selectIndex
          );
        }

        let provideAnswer = provideAnswerTx.decodedResult;

        this.responseSucess("aeVegasProvideAnswer", provideAnswer);
      } catch (e) {
        this.responseError("aeVegasProvideAnswer", e.message);
      }
    },

