    aeVegasMarketDetail: async function (ctAddress, address, owner, marketId) {
      try {
        let vegasContract = await aeSdk.getContractInstance({
          aci: VegasMarketContract,
          contractAddress: ctAddress,
        });

        DRY_RUN_ACCOUNT.pub = address;
        const { decodedResult: marketDetail } =
          await vegasContract.methods.get_market_detail(owner, marketId);

        marketDetail.get_aggregator_user = this.mapToArray(
          marketDetail.get_aggregator_user
        );
        marketDetail.get_oracle_market_record = this.mapToArray(
          marketDetail.get_oracle_market_record
        );
        this.responseSucess("aeVegasMarketDetail", marketDetail);
      } catch (e) {
        this.responseError("aeVegasMarketDetail", e.message);
      }
